/*
$horizontal-menu: center, flex, row, 10vh, flex-start, 0, 0;
$vertical-menu: left, flex, column, 0vh, flex-start, 0, 0;

@mixin menu-orientation ($align-items,$display,$flex-direction,$height,$justify-content,$margin,$padding){ align-items: $align-items; display: $display; flex-direction: $flex-direction; height: $height; justify-content: $justify-content; margin: $margin; padding: $padding; }

header .region-header {
  display: flex;
  background-color: white;
  align-items: center;
}

#block-sitebranding {
  width: span(5);
  background-color: yellow;
  margin-left: gutter();
}

#block-tema-mainnavigation {
  width: span(5);
  background-color: red;
  ul.menu {
   @include menu-orientation($horizontal-menu...);
   li {
     padding-right: 2.2em;
   }
    li:first-of-type {
      padding-left: 1em;
    }
  }
}

#block-tema-languageswitcher {
  ul.links {
  display: flex;
  align-items: center;
  flex-direction: row;
  }
  li {
     padding-right: 1em;
   }
}


