$modularscale: ( base: 1em, ratio: 1.2 );

// Base HTML Elements.
html { box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  min-height: 100%;
  font-size: 18px;
  padding: 0;
  margin: 0;
}

body {
  font-family: $dignitas;
  font-size: 1em;
  line-height: 165%;
}
// Base Headings
//
// Markup: headlines.twig
//
// Style guide: base.headlines
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $heading;
  font-weight: 500;
  font-family: $heading_font;
  padding: 0;
  margin: 0;
}

h1 {
  font-size: ms(5);
  line-height: 165%;
}

h2 {
  font-size: ms(4);
  line-height: 145%;
}

h3 {
  font-size: ms(3);
  line-height: 145%;
}

h4 {
  font-size: ms(2);
  line-height: 145%;
}

h5,
h6 {
  font-size: ms(1);
  line-height: 145%;
}

body .menu-item {
  padding-top: 0;
  margin: 0;
}
// Base Text
//
// Markup: text.twig
//
// Style guide: base.text
a,
a:link,
a:visited {
  text-decoration: none;
  color: $link;
  cursor: inherit;

  &:hover {
    color: $dark-gray;
    text-decoration: none;
    cursor: pointer;
  }
}

body p { margin: 3px 0 10px; }

// Responsive Images
img,
svg {
  max-width: 100%;
  height: auto;
}

// Blockquote & Pre
//
// Markup: blockquote.twig
//
// Style guide: base.blockquote
blockquote {
  padding: 0 15px;
  color: $dark-gray;
  border-left: 4px solid $off-white;
  &:last-child { margin-bottom: 0; }
}

pre {
  white-space: pre-wrap;
  overflow-x: auto;

  code {
    border: 1px dotted $dark-gray;
    background: $off-white;
    padding: 2px 9px;
    display: block;
    font-size: 85%;
  }
}
// List Types
//
// Markup: lists.twig
//
// Style guide: base.lists

// OL items
ol {
  padding: 0;
  margin: 0;

  li {
    margin: 0;
    padding: 0;
  }
}

ol ol { padding: 0; }

ol ol ol { padding: 0; }

// UL items
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
  li {
    margin: 0;
    padding: 0;
  }
}

ul ul { padding: 0; }

ul ul ul { padding: 0; }
// Tables
//
// Markup: table.twig
//
// Style guide: base.table
table {
  width: 100%;

  th {
    font-weight: 600;
    text-align: left;
    background-color: $off-white;
    padding: 4px 5px;
  }

  td {
    border: 0;
    outline: none;
    padding: 4px 5px;
  }

  thead {
    border: 1px solid $off-white;
    border-bottom: 0;
  }

  tbody {
    border: 1px solid $off-white;
  }

  tbody tr:nth-child(even) {
    background-color: $off-white;
    border-top: 1px solid $off-white;
    border-bottom: 1px solid $off-white;
  }
}

// Navigation items
nav ul.menu {
    margin-left: 0;
    list-style-type: none;
    list-style: none;
}

.menu-item--expanded { list-style-image: none; }