@import 'base/typography';

// Colors 
$white: #fff;
$black: #000;
$dark-gray: #919191;
$med-gray: lighten($black, 40%);
$light-gray: lighten($black, 70%);
$lighter-gray: lighten($black, 90%);
$off-white: lighten($black, 97%);

// Utility color variables 
$heading: $black;
$link: $dark-gray;

// Fonts 
$tradegothic: 'TradeGothic';
$tradegothiccond: 'TradeGothic CondEighteen';
$dignitas: 'Dignitas';
$dignitasital: 'Dignitas Italic';
$heading_font: $tradegothiccond;
$body_font: $dignitas;

// Breakpoints & MQ's 
$very-large: 1690px !default;
$large: 1280px !default;
$medium: 980px !default;
$small: 736px !default;
$very-small: 480px !default;

// Susy 
// initial grid
$susy: (
  'columns': susy-repeat(12),
  'gutters': 1,
  'spread': 'narrow',
  'container-spread': 'narrow',
  'svg-grid-colors': hsla(180, 100%, 87%, 1),
);

//body { background: susy-svg-grid() no-repeat scroll; }

body {
  z-index: 99;
  background: red;
}



