@font-face {
    font-family: 'TradeGothic';
    src: url('../fonts/tradegothic/TradeGothic.eot');
    src: local('../fonts/tradegothic/Trade Gothic'), local('../fonts/tradegothic/TradeGothic'),
        url('../fonts/tradegothic/TradeGothic.eot?#iefix') format('embedded-opentype'),
        url('../fonts/tradegothic/TradeGothic.woff2') format('woff2'),
        url('../fonts/tradegothic/TradeGothic.woff') format('woff'),
        url('../fonts/tradegothic/TradeGothic.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'TradeGothic CondEighteen';
    src: url('../fonts/tradegothiccond/TradeGothic-BoldCondTwenty.eot');
    src: local('../fonts/tradegothiccond/Trade Gothic Bold Condensed No.20'), local('../fonts/tradegothiccond/TradeGothic-BoldCondTwenty'),
        url('../fonts/tradegothiccond/TradeGothic-BoldCondTwenty.eot?#iefix') format('embedded-opentype'),
        url('../fonts/tradegothiccond/TradeGothic-BoldCondTwenty.woff2') format('woff2'),
        url('../fonts/tradegothiccond/TradeGothic-BoldCondTwenty.woff') format('woff'),
        url('../fonts/tradegothiccond/TradeGothic-BoldCondTwenty.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Dignitas';
    src: url('../fonts/dignitas/Dignitas-Regular.eot');
    src: local('../fonts/dignitas/Dignitas Regular'), local('../fonts/dignitas/Dignitas-Regular'),
        url('../fonts/dignitas/Dignitas-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/dignitas/Dignitas-Regular.woff2') format('woff2'),
        url('../fonts/dignitas/Dignitas-Regular.woff') format('woff'),
        url('../fonts/dignitas/Dignitas-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Dignitas Italic';
    src: url('../fonts/dignitasital/Dignitas-Italic.eot');
    src: local('../fonts/dignitasital/Dignitas Italic'), local('../fonts/dignitasital/Dignitas-Italic'),
        url('../fonts/dignitasital/Dignitas-Italic.eot?#iefix') format('embedded-opentype'),
        url('../fonts/dignitasital/Dignitas-Italic.woff2') format('woff2'),
        url('../fonts/dignitasital/Dignitas-Italic.woff') format('woff'),
        url('../fonts/dignitasital/Dignitas-Italic.ttf') format('truetype');
    font-weight: normal;
    font-style: italic;
}


