#block-tema-mainnavigation > ul > li,
#block-tema-languageswitcher > ul.links > li,
#block-footer > ul > li,
#block-sitebranding > .site-name, {
  > a {
  font-family: $tradegothiccond;
  font-size: ms(-1);
  @include case(uppercase,.03em);
  color: $black; }
  > a.is-active { color: $black; }
  ul > li > a {
    font-family: $tradegothic;
    font-size: ms(-1);
    color: $dark-gray;
    @include case(none,.02em); }
  :hover { color: $med-gray; }
}

#block-tema-mainnavigation li.menu-item--active-trail a.is-active,
#block-tema-languageswitcher > ul.links > li a.is-active { color: $dark-gray }

#block-footer a.is-active { color: $black; }